* {
  --text-height: 15px;
  --text-gap: 20px;
  --img-width: 40px;
  --img-height: 40px;
  --header-height: 60px;
  --header-main-text-color: #000;
  --header-secondary-text-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.nav_list {
  margin: 0 20px 0 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header-img {
  height: var(--img-height);
  width: var(--img-width);
}

.nav_links {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  height: var(--header-height);
  line-height: var(--header-height);
  color: var(--header-main-text-color);
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav_list:last-child {
  margin: 0;
}

.nav_link:hover, .nav_link:focus {
  color: var(--header-secondary-text-color);
  transition: all .2s;
}

.nav_link:after {
  content: "";
  opacity: 0;
  background-color: var(--header-secondary-text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: all .2s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav_link:focus:after, .is-active {
  color: #00acdc;
  opacity: 1;
}

.is-active:after {
  opacity: 1;
}
